.Li {
  margin: 0px;
  padding: 0px;
}
.Li .container h1 {
  font-weight: bold;
  margin: 0;
}

.Li .container p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}
.Li .container span {
  font-size: 12px;
}

.Li .container a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.Li .container button {
  border-radius: 20px;
  border: 1px solid #071556;
  background-color: #071556;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.Li .container button:active {
  transform: scale(0.95);
}

.Li .container button:focus {
  outline: none;
}

.Li .container button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}

.Li .container form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.Li .container input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.social-container {
  margin: 10px 5px;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.Li .container {
  background-color: #ffffff;
  border-radius: 10px;
  /* box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22); */
  position: relative;
  overflow: hidden;
  /* width: 768px; */
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.Li .container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.Li .container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.Li .container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #1a2f8b;
  background: -webkit-linear-gradient(to right, #071556, #1a2f8b);
  background: linear-gradient(to right, #071556, #1a2f8b);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.Li .container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.Li .container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.Li .container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

@media screen and (max-width: 543px) {
  .sign-in-container {
    width: 100%;
  }
  .sign-up-container {
    width: 100%;
    /* z-index: 5; */
    opacity: unset;
  }
  .Li .container a {
    margin: 5px;
  }
  .Li .container form {
    padding: 0 25px;
  }
  .Li .container button {
    padding: 5px 25px;
  }
}
